.list-header-container {
    width: 100%;
    padding: 0 1em;
    box-sizing: border-box;

    &.light {
        background-color: #f0f0f0;
        border-bottom: solid 1px #dadada;
    }

    &.dark {
        background-color: #232323;
        border-bottom: solid 1px #383838;
    }

    .title-and-theme {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    .list-title {
        margin: 16px 0;

        &.light {
            color: #2b2b2b;
        }

        &.dark {
            color: #f4f4f4;
        }
    }

    .list-description {
        margin: 0;
        margin-bottom: 16px;

        &.light {
            color: #505050;
        }

        &.dark {
            color: #bbbbbb;
        }
    }
}
